/* Even darker base styles */
body {
  margin: 0;
  font-family: 'Inter', 'Roboto', Arial, sans-serif;
  background: #0a0a12;
  color: #F3F4F6;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

/* Fade-in animation for sections */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

section, .footer, .navbar {
  animation: fadeInUp 1.2s cubic-bezier(0.22, 1, 0.36, 1);
}

/* Navbar improvements */
.navbar {
  background: rgba(16, 24, 32, 0.55);
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 2px 24px 0 rgba(0,0,0,0.22);
  backdrop-filter: blur(18px) saturate(1.5);
  border-bottom: 1.5px solid rgba(255,255,255,0.08);
  border-radius: 0 0 18px 18px;
  overflow: hidden;
}
.navbar::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(90deg, rgba(255,255,255,0.08) 0%, rgba(34,197,94,0.04) 100%);
  pointer-events: none;
  z-index: 0;
}
.navbar-inner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 64px;
  z-index: 1;
}
.navbar-brand {
  display: flex;
  align-items: center;
  font-weight: 800;
  font-size: 1.25rem;
  color: #22c55e;
  letter-spacing: 0.01em;
}
.brand-name {
  font-size: 1.18rem;
  font-weight: 700;
  color: #22c55e;
  letter-spacing: 0.01em;
}
.nav-links {
  list-style: none;
  display: flex;
  gap: 2.2rem;
  margin: 0;
  padding: 0;
  flex: 1;
  justify-content: center;
}
.nav-links li a {
  text-decoration: none;
  color: #F3F4F6;
  font-weight: 500;
  font-size: 1.05rem;
  transition: color 0.2s, text-shadow 0.2s;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
}
.nav-links li a:hover, .nav-links li a.active {
  color: #22c55e;
  text-shadow: 0 0 8px #22c55e88;
  background: rgba(34,197,94,0.08);
}
.navbar-cta {
  margin-left: 1.5rem;
  font-size: 1.05rem;
  padding: 0.5rem 1.2rem;
  border-radius: 8px;
  font-weight: 700;
  background: linear-gradient(90deg, #22c55e 0%, #4ade80 100%);
  color: #10101a;
  box-shadow: 0 2px 16px 0 #22c55e55;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  text-decoration: none;
  display: inline-block;
}
.navbar-cta:hover {
  background: linear-gradient(90deg, #4ade80 0%, #22c55e 100%);
  color: #fff;
  box-shadow: 0 4px 32px 0 #22c55eaa;
}

/* Remove section backgrounds and blend sections */
.hero, .overview, .features, .applications, .techspecs, .team, .contact, .gallery {
  min-height: 60vh;
  padding-top: 4rem;
  padding-bottom: 4rem;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Section blending: add gradient overlays between sections */
.hero::after, .overview::after, .features::after, .applications::after, .techspecs::after, .team::after {
  display: none !important;
}

/* Remove hard lines from footer */
.footer {
  background: rgba(10,10,18,0.92);
  border-top: none;
  box-shadow: 0 -2px 16px 0 rgba(0,0,0,0.18);
  backdrop-filter: blur(8px);
}

/* Hero */
.hero {
  text-align: center;
  padding: 6rem 1rem 4rem 1rem;
  /* background: linear-gradient(120deg, #0a0a12 60%, #18181b 100%); */
  z-index: 1;
  position: relative;
}
.hero h1 {
  font-size: 2.8rem;
  font-weight: 900;
  margin-bottom: 1.2rem;
  color: #fff;
  letter-spacing: 0.01em;
  text-shadow: 0 2px 24px #38bdf822;
}
.hero p {
  font-size: 1.35rem;
  color: #b6c2d1;
  margin-bottom: 2.2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* Overview */
.overview {
  background: #10101a;
  padding: 3.5rem 0 2.5rem 0;
  text-align: center;
  z-index: 1;
}
.overview h2 {
  color: #22c55e;
  font-size: 2.1rem;
  margin-bottom: 1.2rem;
  font-weight: 800;
}
.overview p {
  color: #b6c2d1;
  font-size: 1.18rem;
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.7;
}

/* Features */
.features {
  padding: 3.5rem 0 3rem 0;
  background: #18181b;
}
.features-container {
  display: flex;
  gap: 2.5rem;
  justify-content: center;
  flex-wrap: wrap;
}
.feature-card {
  background: linear-gradient(120deg, #10101a 60%, #18181b 100%);
  border: 1.5px solid #23272f;
  border-radius: 16px;
  box-shadow: 0 4px 32px 0 #0ea5e933, 0 1.5px 8px 0 #000a  ;
  padding: 2.2rem 1.7rem;
  flex: 1 1 260px;
  max-width: 340px;
  min-width: 220px;
  text-align: left;
  color: #F3F4F6;
  transition: transform 0.3s, box-shadow 0.3s;
  animation: fadeInUp 1.2s cubic-bezier(0.22, 1, 0.36, 1);
}
.feature-card:hover {
  transform: translateY(-10px) scale(1.03);
  box-shadow: 0 8px 48px 0 #38bdf8aa, 0 2px 16px 0 #000a;
  border-color: #38bdf8;
}
.feature-card h3 {
  margin-top: 0;
  color: #22c55e;
  font-size: 1.22rem;
  font-weight: 700;
  letter-spacing: 0.01em;
}
.feature-card p {
  color: #b6c2d1;
  font-size: 1.05rem;
}

/* Footer */
.footer {
  background: #10101a;
  padding: 2.5rem 0 1.2rem 0;
  text-align: center;
  color: #64748b;
  font-size: 1rem;
  border-top: 1px solid #18181b;
  z-index: 1;
}

/* Ensure particles background stays behind everything */
#tsparticles {
  position: fixed !important;
  z-index: 0 !important;
  top: 0;
  left: 0;
  width: 100vw !important;
  height: 100vh !important;
  pointer-events: none;
}

html {
  scroll-behavior: smooth;
}

.hero-btn-row {
  display: flex;
  justify-content: center;
  margin-top: 2.2rem;
}
.learnmore-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(34,197,94,0.18);
  color: #22c55e;
  font-weight: 700;
  font-size: 1rem;
  padding: 0.55rem 1.4rem;
  min-width: 120px;
  border-radius: 999px;
  box-shadow: 0 2px 16px 0 #22c55e55, 0 0 0 2px #22c55e inset;
  text-decoration: none;
  border: 1.5px solid rgba(34,197,94,0.5);
  cursor: pointer;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s, transform 0.15s, border 0.2s;
  outline: none;
  position: relative;
  z-index: 2;
  backdrop-filter: blur(8px) saturate(1.5);
}
.learnmore-btn:hover, .learnmore-btn:focus {
  background: linear-gradient(90deg, #4ade80 0%, #22c55e 100%);
  color: #fff;
  box-shadow: 0 4px 32px 0 #22c55eaa, 0 0 0 4px #22c55ecc;
  border: 1.5px solid #22c55e;
  transform: translateY(-2px) scale(1.07);
}
.learnmore-btn .btn-arrow {
  display: inline-block;
  vertical-align: middle;
  margin-left: 0.2rem;
  transition: transform 0.2s;
}
.learnmore-btn:hover .btn-arrow, .learnmore-btn:focus .btn-arrow {
  transform: translateX(4px);
}

/* Rectangular line animation for background */
.line-anim {
  position: absolute;
  top: 10vh;
  left: 10vw;
  width: 80vw;
  height: 80vh;
  z-index: 2;
  pointer-events: none;
}
.line-anim rect {
  fill: none;
  stroke: #38bdf8;
  stroke-width: 3;
  stroke-dasharray: 2400;
  stroke-dashoffset: 2400;
  rx: 32;
  animation: rect-dash 4s linear infinite;
}
@keyframes rect-dash {
  to {
    stroke-dashoffset: 0;
  }
}

.section-animate {
  animation: fadeInUp 1.2s cubic-bezier(0.22, 1, 0.36, 1);
}

.section-animate .section-child {
  opacity: 0;
  transform: translateY(40px);
  animation: fadeInUp 1s cubic-bezier(0.22, 1, 0.36, 1) forwards;
}
.section-animate .section-child:nth-child(1) { animation-delay: 0.2s; }
.section-animate .section-child:nth-child(2) { animation-delay: 0.4s; }
.section-animate .section-child:nth-child(3) { animation-delay: 0.6s; }
.section-animate .section-child:nth-child(4) { animation-delay: 0.8s; }
.section-animate .section-child:nth-child(5) { animation-delay: 1s; }

.gallery-grid img {
  transition: transform 0.2s, box-shadow 0.2s;
}
.gallery-grid img:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 32px #22c55e55;
}

.team-grid img {
  border: 2px solid #22c55e33;
  transition: border 0.2s, transform 0.2s;
}
.team-grid img:hover {
  border: 2px solid #22c55e;
  transform: scale(1.07);
}
