.animated-text {
  color: #fff;
  font-weight: 700;
  text-shadow: 0 2px 16px #0006, 0 1px 1px #2228;
}

.animated-text span {
  opacity: 0;
  transform: translateY(24px);
  display: inline-block;
  animation: animatedTextFadeInUp 0.6s cubic-bezier(0.22, 1, 0.36, 1) forwards;
}

@keyframes animatedTextFadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
} 